util.isWebsite = function(url) {
  return url &&
    /^(https?|ftp|file):\/\/[-A-Za-z0-9+&@#/%?=~_|!:,.;]+[-A-Za-z0-9+&@#/%=~_|]/.test(
      url
    )
    ? true
    : false
}
