function jsGetAge(strBirthday) {
  var returnAge
  var strBirthdayArr = strBirthday.split('-')
  var birthYear = strBirthdayArr[0]
  var birthMonth = strBirthdayArr[1]
  var birthDay = strBirthdayArr[2]

  var d = new Date()
  var nowYear = d.getFullYear()
  var nowMonth = d.getMonth() + 1
  var nowDay = d.getDate()

  if (nowYear == birthYear) {
    returnAge = 0 // 0
  } else {
    var ageDiff = nowYear - birthYear //
    if (ageDiff > 0) {
      if (nowMonth == birthMonth) {
        var dayDiff = nowDay - birthDay //
        if (dayDiff < 0) {
          returnAge = ageDiff - 1
        } else {
          returnAge = ageDiff
        }
      } else {
        var monthDiff = nowMonth - birthMonth //
        if (monthDiff < 0) {
          returnAge = ageDiff - 1
        } else {
          returnAge = ageDiff
        }
      }
    } else {
      returnAge = -1 //-1  }
    }
  }
  return returnAge //
}

// 
util.checkIDCard = function(card) {
  //
  if (card === '') {
    return false
  }
  //
  if (isCardNo(card) === false) {
    return false
  }
  //
  if (checkProvince(card) === false) {
    return false
  }
  //
  if (checkBirthday(card) === false) {
    return false
  }
  //
  if (checkParity(card) === false) {
    return false
  }
  return true
}

//
util.jsGetAge = function(strBirthday) {
  if (!strBirthday) {
    return ''
  }
  var returnAge
  var strBirthdayArr = strBirthday.split('-')
  var birthYear = strBirthdayArr[0]
  var birthMonth = strBirthdayArr[1]
  var birthDay = strBirthdayArr[2]

  var d = new Date()
  var nowYear = d.getFullYear()
  var nowMonth = d.getMonth() + 1
  var nowDay = d.getDate()

  if (nowYear == birthYear) {
    returnAge = 0 // 0
  } else {
    var ageDiff = nowYear - birthYear //
    if (ageDiff > 0) {
      if (nowMonth == birthMonth) {
        var dayDiff = nowDay - birthDay //
        if (dayDiff < 0) {
          returnAge = ageDiff - 1
        } else {
          returnAge = ageDiff
        }
      } else {
        var monthDiff = nowMonth - birthMonth //
        if (monthDiff < 0) {
          returnAge = ageDiff - 1
        } else {
          returnAge = ageDiff
        }
      }
    } else {
      returnAge = -1 //-1  }
    }
  }
  return returnAge //
}
// 
util.Format = function(date) {
  const dt = new Date(date)

  const y = dt.getFullYear()
  const m = (dt.getMonth() + 1 + '').padStart(2, '0')
  const d = (dt.getDate() + '').padStart(2, '0')

  const hh = (dt.getHours() + '').padStart(2, '0')
  const mm = (dt.getMinutes() + '').padStart(2, '0')
  const ss = (dt.getSeconds() + '').padStart(2, '0')
  return `${y}-${m}-${d} ${hh}:${mm}:${ss}`
}
// 
util.compareTime = function(startTime, endTime) {
  let result = Date.parse(startTime) - Date.parse(endTime)
  if (result < 0) {
    return 'small'
  } else if (result > 0) {
    return 'big'
  } else if (result == 0) {
    return 'same'
  } else {
    return 'exception'
  }
}

/**
 * DataLength
 * 
 * fData
 * fData(Unicode2Unicode1)
 */
util.dataLength = function(data) {
  var intLength = 0
  for (var i = 0; i < data.length; i++) {
    if (data.charCodeAt(i) < 0 || data.charCodeAt(i) > 255)
      intLength = intLength + 2
    else intLength = intLength + 1
  }
  return intLength
}
//
util.extend = function(target, source) {
  for (var obj in source) {
    target[obj] = source[obj]
  }
  return target
}
export default util
